import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router);

// 异步加载组件(app.js比较小时(一般小于1MB）不建议用异步加载)，组件内部也可以这样处理异步
export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      // redirect: '/index',
      component: () => import('@/views/index'),
    },
    // 1.信访工作形势研判
    {
      path: '/xfgzxsyp',
      name: 'xfgzxsyp',
      redirect: '/xfgzxsyp/xfbm',
      component: () => import('@/views/xfgzxsyp/index'),
      children: [{
          path: 'xfbm',
          name: 'xfbm',
          component: () => import('@/views/xfgzxsyp/erJiViews/xfbm')
        },
        {
          path: 'zrdw',
          name: 'zrdw',
          component: () => import('@/views/xfgzxsyp/erJiViews/zrdw')
        }
      ]
    },
    // 2.信访态势统计分析
    {
      path: '/xftstjfx',
      name: 'xftstjfx',
      redirect: '/xftstjfx/zlfx',
      component: () => import('@/views/xftstjfx/index'),
      children: [{
          path: 'zlfx',
          name: 'zlfx',
          component: () => import('@/views/xftstjfx/erJiViews/zlfx')
        },
        {
          path: 'nrfl',
          name: 'nrfl',
          redirect: '/xftstjfx/nrfl/dyfb',
          component: () => import('@/views/xftstjfx/erJiViews/nrfl'),
          children: [{
            path: 'dyfb',
            name: 'dyfb',
            component: () => import('@/views/xftstjfx/sanJiViews/dyfb')
          },
          {
            path: 'wtfl',
            name: 'wtfl',
            component: () => import('@/views/xftstjfx/sanJiViews/wtfl')
          }
        ]},
        {
          path: 'xfxs',
          name: 'xfxs',
          redirect: '/xftstjfx/xfxs/lx',
          component: () => import('@/views/xftstjfx/erJiViews/xfxs'),
          children: [{
            path: 'lx',
            name: 'lx',
            component: () => import('@/views/xftstjfx/sanJiViews/lx')
          },
          {
            path: 'lf',
            name: 'lf',
            component: () => import('@/views/xftstjfx/sanJiViews/lf')
          },
          {
            path: 'wx',
            name: 'wx',
            component: () => import('@/views/xftstjfx/sanJiViews/wx')
          }
        ]},
        {
          path: 'xfmd',
          name: 'xfmd',
          component: () => import('@/views/xftstjfx/erJiViews/xfmd')
        },
        {
          path: 'cjcj',
          name: 'cjcj',
          redirect: '/xftstjfx/cjcj/chuj',
          component: () => import('@/views/xftstjfx/erJiViews/cjcj'),
          children: [{
            path: 'chuj',
            name: 'chuj',
            component: () => import('@/views/xftstjfx/sanJiViews/chuj')
          },
          {
            path: 'chongj',
            name: 'chongj',
            component: () => import('@/views/xftstjfx/sanJiViews/chongj')
          },
        ]},
      ]
    },
     // 3.热点模型分析
     {
      path: '/rdmxfx',
      name: 'rdmxfx',
      redirect: '/rdmxfx/jjf',
      component: () => import('@/views/rdmxfx/index'),
      children:[
        {
          path: 'jjf',
          name: 'jjf',
          component: () => import('@/views/rdmxfx/erJiViews/jjf')
        },
        {
          path: 'dsf',
          name: 'dsf',
          component: () => import('@/views/rdmxfx/erJiViews/dsf')
        },
        {
          path: 'jtf',
          name: 'jtf',
          component: () => import('@/views/rdmxfx/erJiViews/jtf')
        },
        {
          path: 'qt',
          name: 'qt',
          redirect: '/rdmxfx/qt/jjf',
          component: () => import('@/views/rdmxfx/erJiViews/qt'),
          children:[
            {
              path: 'jjf',
              name: 'jjf',
              component: () => import('@/views/rdmxfx/sanJiViews/jjf')
            },
            {
              path: 'dsf',
              name: 'dsf',
              component: () => import('@/views/rdmxfx/sanJiViews/dsf')
            }
          ]
        }
      ]
    },
    // 4.政策关联分析
    {
      path: '/zcglfx',
      name: 'zcglfx',
      redirect: '/zcglfx/sjfx',
      component: () => import('@/views/zcglfx/index'),
      children: [{
          path: 'sjfx',
          name: 'sjfx',
          component: () => import('@/views/zcglfx/erJiViews/sjfx')
        },
        {
          path: 'zctj',
          name: 'zctj',
          component: () => import('@/views/zcglfx/erJiViews/zctj')
        }
      ]
    },
    
    // 5.重点信访人员分布
    {
      path: '/zdxfryfb',
      name: 'zdxfryfb',
      redirect: '/zdxfryfb/zlfx',
      component: () => import('@/views/zdxfryfb/index'),
      children:[
        {
          path: 'zlfx',
          name: 'zlfx',
          component: () => import('@/views/zdxfryfb/erJiViews/zlfx')
        }
      ]
    },
    // 6.信访形势预警预测
    {
      path: '/xfxsyjyc',
      name: 'xfxsyjyc',
      redirect: '/xfxsyjyc/yjxs',
      component: () => import('@/views/xfxsyjyc/index'),
      children: [{
          path: 'yjxs',
          name: 'yjxs',
          component: () => import('@/views/xfxsyjyc/erJiViews/yjxs')
        },
        {
          path: 'ycxs',
          name: 'ycxs',
          component: () => import('@/views/xfxsyjyc/erJiViews/ycxs')
        }
      ]
    },
    // 7.信访行为分析预警
    {
      path: '/xfxwfxyj',
      name: 'xfxwfxyj',
      redirect: '/xfxwfxyj/xwzhyj',
      component: () => import('@/views/xfxwfxyj/index'),
      children: [{
          path: 'xwzhyj',
          name: 'xwzhyj',
          component: () => import('@/views/xfxwfxyj/erJiViews/xwzhyj')
        },
        {
          path: 'xwgjyj',
          name: 'xwgjyj',
          component: () => import('@/views/xfxwfxyj/erJiViews/xwgjyj')
        }
      ]
    },
    
    // 这里是为了让输入错误路径和空路径时默认显示的内容
    {
      path: '*',
      redirect: '/'
    }
  ]
})