import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store/index'




import '@/styles/reset.css'; // 初始化样式
import 'element-ui/lib/theme-chalk/index.css';
import './styles/resetElementUI.css'; // 初始化样式
import {
  Scrollbar,
  Autocomplete,
  DatePicker,
  Table,
  TableColumn,
  Select,
  Option,
  Loading
} from 'element-ui';
Vue.use(Scrollbar)
Vue.use(Autocomplete)
Vue.use(DatePicker)
Vue.use(Table)
Vue.use(TableColumn)
Vue.use(Select)
Vue.use(Option)
Vue.use(Loading)
Vue.use(Table)


// vue.observable替代vuex
