// 全局引入echarts
// 使用:this.$echarts.init()
import echarts from 'echarts';
Vue.prototype.$echarts = echarts;

import AmCharts from 'amcharts3';
import standard from "./utils/standard"
Vue.use(standard)
Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App),
}).$mount('#app')